import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { PitConfigService } from '../../pit/service/pit-config.service';
import { LapService } from '../../lap/service/lap.service';
import { combineLatest } from 'rxjs';
import { takeUntilDestroyed, toObservable } from '@angular/core/rxjs-interop';
import { DriverConfigService } from '../../driver/service/driver-config.service';
import {
  LapSimulatorConfigService
} from '../../lap-simulator/service/lap-simulator-config.service';

@Injectable({
  providedIn: 'root'
})
export class PitSimulatorService {

  private readonly lapService = inject(LapService);
  private readonly pitConfig = inject(PitConfigService);
  private readonly driverConfigService = inject(DriverConfigService);
  private readonly lapSimulatorConfigService = inject(LapSimulatorConfigService);

  private readonly worker: Worker | undefined;

  readonly pitDoneByRaceNumber: WritableSignal<Map<number, number>> = signal(new Map<number, number>());
  readonly remainingPitByRaceNumber: WritableSignal<Map<number, number>> = signal(new Map<number, number>());
  readonly remainingPitTimeMillisecondsByRaceNumber: WritableSignal<Map<number, number>> = signal(new Map<number, number>());

  constructor() {

    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./pit-simulator.worker', import.meta.url));
      this.worker.onmessage = ({data}) => {
        this.pitDoneByRaceNumber.set(data.pitDoneByRaceNumber);
        this.remainingPitByRaceNumber.set(data.remainingPitByRaceNumber);
        this.remainingPitTimeMillisecondsByRaceNumber.set(data.remainingPitTimeMillisecondsByRaceNumber);
      };
    }

    combineLatest({
      pitLaps: toObservable(this.lapService.pitLaps),
      pacesByRaceNumber: toObservable(this.lapService.pacesByRaceNumber),
      pitConfig: toObservable(this.pitConfig.pitConfig),
      driverConfig: toObservable(this.driverConfigService.driverConfig),
      lapSimulatorConfig: toObservable(this.lapSimulatorConfigService.lapSimulatorConfig),
    })
    .pipe(takeUntilDestroyed())
    .subscribe(({
                  pitLaps,
                  pacesByRaceNumber,
                  pitConfig,
                  driverConfig,
                  lapSimulatorConfig
    }) => {
      if (this.worker && pitConfig && driverConfig && lapSimulatorConfig) {
        this.worker.postMessage({
          pitLaps: pitLaps,
          pacesByRaceNumber: pacesByRaceNumber,
          pitConfig: pitConfig,
          driverConfig: driverConfig,
          lapSimulatorConfig: lapSimulatorConfig,
        });
      }
    });
  }
}
