import { inject, Injectable, signal, WritableSignal } from '@angular/core';
import { PitSimulatorService } from '../../pit-simulator/service/pit-simulator.service';
import { LapSimulatorService } from '../../lap-simulator/service/lap-simulator.service';
import { combineLatest } from 'rxjs';
import { takeUntilDestroyed, toObservable } from '@angular/core/rxjs-interop';

@Injectable({
  providedIn: 'root'
})
export class RaceSimulatorService {

  private readonly lapSimulatorService = inject(LapSimulatorService);
  private readonly pitSimulatorService = inject(PitSimulatorService);

  private readonly worker: Worker | undefined;

  readonly virtualRacePositionByRaceNumber: WritableSignal<Map<number, number>> = signal(new Map<number, number>());

  constructor() {
    if (typeof Worker !== 'undefined') {
      this.worker = new Worker(new URL('./race-simulator.worker', import.meta.url));
      this.worker.onmessage = ({data}) => {
        this.virtualRacePositionByRaceNumber.set(data.virtualRacePositionByRaceNumber);
      };
    }

    combineLatest({
      virtualLastCumulatedTimeByRaceNumber: toObservable(this.lapSimulatorService.virtualLastCumulatedTimeByRaceNumber),
      remainingPitTimeMillisecondsByRaceNumber: toObservable(this.pitSimulatorService.remainingPitTimeMillisecondsByRaceNumber)
    })
    .pipe(takeUntilDestroyed())
    .subscribe((
      {
        virtualLastCumulatedTimeByRaceNumber,
        remainingPitTimeMillisecondsByRaceNumber
      }) => {
      if (this.worker) {
        this.worker.postMessage({
          virtualLastCumulatedTimeByRaceNumber: virtualLastCumulatedTimeByRaceNumber,
          remainingPitTimeMillisecondsByRaceNumber: remainingPitTimeMillisecondsByRaceNumber,
        });
      }
    });
  }
}
