import { Component, computed, inject, Signal, signal } from '@angular/core';
import { NavigatorComponent } from "../../../shared/component/navigator/navigator.component";
import { Title } from '@angular/platform-browser';
import { CardComponent } from '../../../shared/component/card/card.component';
import { TeamDataService } from '../../../team/service/team-data.service';
import { Team } from '../../../team/model/team';
import { NgClass, NgForOf } from '@angular/common';
import { RacePositionService } from '../../../race/service/race-position.service';
import { PitSimulatorService } from '../../../pit-simulator/service/pit-simulator.service';
import {
  millisecondsToHourMinuteSecondMillisecondString,
  millisecondsToMinuteSecondMillisecondsString
} from '../../../util/date.util';
import { Tooltip } from '../../../shared/component/tooltip/tooltip';
import { TooltipPosition } from '../../../shared/component/tooltip/tooltip.enums';
import { TooltipDirective } from '../../../shared/component/tooltip/tooltip.directive';
import { TeamService } from '../../../team/service/team.service';
import { LapService } from '../../../lap/service/lap.service';
import { RaceSimulatorService } from '../../../race-simulator/service/race-simulator.service';
import { GapSimulatorService } from '../../../gap-simulator/service/gap-simulator.service';

type SortColumn = 'position' | 'raceNumber' | 'finalPosition';
type SortDirection = 'asc' | 'desc';

interface SortState {
  column: SortColumn;
  direction: SortDirection;
}

@Component({
  selector: 'app-live-ranking',
  imports: [
    NavigatorComponent,
    CardComponent,
    NgForOf,
    TooltipDirective,
    NgClass,
  ],
  templateUrl: './live-ranking.component.html',
  styleUrl: './live-ranking.component.css'
})
export class LiveRankingComponent {
  private readonly titleService = inject(Title);
  private readonly teamDataService = inject(TeamDataService);
  private readonly teamService = inject(TeamService);
  private readonly racePositionService = inject(RacePositionService);
  private readonly pitSimulatorService = inject(PitSimulatorService);
  private readonly raceSimulatorService = inject(RaceSimulatorService);
  private readonly lapService = inject(LapService);
  private readonly gapSimulatorService = inject(GapSimulatorService);

  // Sorting state management
  private readonly sortState = signal<SortState>({ column: 'position', direction: 'asc' });

  readonly teams: Signal<Team[]> = computed(() => {
    const allTeams = this.teamDataService.teams();
    const racePositionByRaceNumber = this.racePositionService.racePositionByRaceNumber();
    const currentSort = this.sortState();

    return allTeams.slice().sort((a, b) => {
      let comparison = 0;

      switch (currentSort.column) {
        case 'position':
          const positionA = racePositionByRaceNumber.get(a.raceNumber);
          const positionB = racePositionByRaceNumber.get(b.raceNumber);

          // Teams without position go to the end
          if (positionA === undefined && positionB === undefined) {
            comparison = a.raceNumber - b.raceNumber; // Sort by race number as fallback
          } else if (positionA === undefined) {
            comparison = 1;
          } else if (positionB === undefined) {
            comparison = -1;
          } else {
            comparison = positionA - positionB;
          }
          break;

        case 'raceNumber':
          comparison = a.raceNumber - b.raceNumber;
          break;

        case 'finalPosition':
          const finalPositionA = this.getVirtualPosition(a.raceNumber);
          const finalPositionB = this.getVirtualPosition(b.raceNumber);

          // Handle string/number comparison for final position
          const numA = typeof finalPositionA === 'number' ? finalPositionA : Number.MAX_SAFE_INTEGER;
          const numB = typeof finalPositionB === 'number' ? finalPositionB : Number.MAX_SAFE_INTEGER;
          comparison = numA - numB;
          break;
      }

      // Apply sort direction
      return currentSort.direction === 'asc' ? comparison : -comparison;
    });
  });

  readonly activeTeamId = computed(() => {
    const activeTeam = this.teamService.activeTeam();
    return activeTeam?.id;
  });

  protected tooltipPositionData: Tooltip = {
    footer: "",
    paragraphs: ["Position"],
    title: ""
  }

  protected tooltipRaceNumberData: Tooltip = {
    footer: "",
    paragraphs: ["Race Number"],
    title: ""
  }

  protected tooltipCalculatedData: Tooltip = {
    footer: "",
    paragraphs: ["Calculated from extracted laps"],
    title: ""
  }

  protected tooltipPredictedData: Tooltip = {
    footer: "",
    paragraphs: ["Predicted"],
    title: ""
  }
  protected tooltipPosition: TooltipPosition = TooltipPosition.ABOVE;

  smallScreen: boolean = false;

  constructor() {
    this.titleService.setTitle('WBR - Live ranking');
    this.onResize();
    window.addEventListener('resize', this.onResize.bind(this));
  }

  private onResize() {
    this.smallScreen = 850 > window.innerWidth;
  }

  getRacePosition(raceNumber: number): number | string {
    const racePositionByRaceNumber = this.racePositionService.racePositionByRaceNumber()
    return racePositionByRaceNumber.get(raceNumber) ?? '';
  }

  getLastLapNumber(raceNumber: number): number {
    const lastLapNumberByRaceNumber = this.lapService.lastLapNumberByRaceNumber();
    return lastLapNumberByRaceNumber.get(raceNumber) ?? 0;
  }

  getVirtualGap(raceNumber: number): string {
    const virtualGapByRaceNumber = this.gapSimulatorService.virtualGapByRaceNumber();
    let gapMilliseconds = virtualGapByRaceNumber.get(raceNumber);
    if (gapMilliseconds) {
      const prefix = gapMilliseconds > 0 ? '+ ' : '- ';
      gapMilliseconds = Math.abs(gapMilliseconds);
      if (gapMilliseconds >= 3600000) {
        return prefix + millisecondsToHourMinuteSecondMillisecondString(gapMilliseconds);
      } else {
        return prefix + millisecondsToMinuteSecondMillisecondsString(gapMilliseconds);
      }
    }
    return '--:--.---'

  }

  getPitDone(raceNumber: number): number {
    const pitDoneByRaceNumber = this.pitSimulatorService.pitDoneByRaceNumber();
    return pitDoneByRaceNumber.get(raceNumber) ?? 0;
  }

  getRemainingPit(raceNumber: number): number {
    const remainingPitByRaceNumber = this.pitSimulatorService.remainingPitByRaceNumber();
    return remainingPitByRaceNumber.get(raceNumber) ?? 0;
  }

  getRemainingPitTimeString(raceNumber: number): string {
    const remainingPitTimeMillisecondsByRaceNumber = this.pitSimulatorService.remainingPitTimeMillisecondsByRaceNumber();
    const value = remainingPitTimeMillisecondsByRaceNumber.get(raceNumber);
    if (value) {
      if (value >= 3600000) {
        millisecondsToHourMinuteSecondMillisecondString(value);
      } else {
        return millisecondsToMinuteSecondMillisecondsString(value);
      }
    }
    return '--:--.---'
  }

  getVirtualPosition(raceNumber: number): number | string {
    const virtualRacePositionByRaceNumber = this.raceSimulatorService.virtualRacePositionByRaceNumber();
    return virtualRacePositionByRaceNumber.get(raceNumber) || 0;
  }

  // Sorting methods
  sortBy(column: SortColumn): void {
    const currentSort = this.sortState();

    if (currentSort.column === column) {
      // Toggle direction if same column
      this.sortState.set({
        column,
        direction: currentSort.direction === 'asc' ? 'desc' : 'asc'
      });
    } else {
      // Set new column with ascending direction
      this.sortState.set({
        column,
        direction: 'asc'
      });
    }
  }

  getSortIcon(column: SortColumn): string {
    const currentSort = this.sortState();

    if (currentSort.column !== column) {
      return '‚ÜïÔ∏è'; // Neutral sort icon
    }

    return currentSort.direction === 'asc' ? '‚¨ÜÔ∏è' : '‚¨áÔ∏è';
  }

  isSortedBy(column: SortColumn): boolean {
    return this.sortState().column === column;
  }

}
